.header {
    display: flex;
    padding-top: 12px;
    align-items: center;
    justify-content: space-between;

    @media screen and (min-width: $tablet) {
        justify-content: flex-start;
        padding-top: 20px;
    }

    @media screen and (min-width: $desctop) {
        padding-top: 28px;
    }
}


.btn-filled__header {
    margin-left: 32px;

    @media screen and (max-width: ($tablet - 1)) {
        display: none;
    }

    @media screen and (min-width: $desctop) {
        margin-left: auto;
    }
}

.menu {
    display: flex;
    margin-left: 268px;
    padding: 12px 0;
}

@media screen and (max-width: ($desctop - 1)) {
    .menu {
        display: none;
    }
}

.menu__item {
    cursor: pointer;

    &:not(:last-child) {
        margin-right: 27px;
    }
}

.menu__link {
    font-weight: 700;
    font-size: 16px;
    line-height: 1.25;
    color: $text-light-color;

    &:hover,
    &:focus {
        color: $brand-second-color;
    }
}