.backdrop-franchise {
    position: fixed;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    background-color: rgba(156, 190, 143, 0.5);
    z-index: 10000;
    transition: opacity $transition, visibility $transition;

    &.is-hidden {
        opacity: 0;
        visibility: hidden;
        pointer-events: none;
    }

}