.products {
    background-image: url("../images/mobile/bg-fourth-screen-1x-min.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: left top;
    margin-left: auto;
    margin-right: auto;
    padding-top: 60px;
    padding-bottom: 60px;
}
@media screen and (min-device-pixel-ratio: 2) and (max-width:767px),
screen and (min-resolution: 192dpi) and (max-width:767px),
screen and (min-resolution: 2dppx) and (max-width:767px) {
    .products {
        background-image: url("../images/mobile/bg-fourth-screen-2x-min.jpg");
    }
    .products__item:nth-child(1)::after {
        background-image: url("../images/mobile/products-mobile-png/products01@2x.png");
    }
    .products__item:nth-child(2)::after {
        background-image: url("../images/mobile/products-mobile-png/products02@2x.png");
    }
    .products__item:last-child::after {
        background-image: url("../images/mobile/products-mobile-png/products03@2x.png");
    }
}
@media screen and (min-device-pixel-ratio: 3) and (max-width:767px),
screen and (min-resolution: 288dpi) and (max-width:767px),
screen and (min-resolution: 3dppx) and (max-width:767px) {
    .products {
        background-image: url("../images/mobile/bg-fourth-screen-3x-min.jpg");
    }
}
.products__container {
    width: 100%;
    padding-left: 20px;
    padding-right: 20px;
    margin-right: auto;
    margin-left: auto;
}

@media screen and (min-width:480px) {
    .products__container {
        width: 480px;
    }
}
.section__sup-title {
    @include font(800, 18px, 22px, 0.04em);
    text-align: center;
    text-transform: uppercase;

    margin-bottom: 12px;
}

.section__title {
    font-family: $font-title;
    @include font(400, 30px, 38px, 0.04em);
    text-align: center;
    text-transform: uppercase;

    margin-bottom: 148px;
}

.products__list {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    row-gap: 164px;
}

.products__img {
    position: relative;
        display: block;
}

.products__item{
    flex: 1;
    position: relative;
    padding: 123px 0 12px 0;
    text-align: center;
    min-width: 280px;
    box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
    border-radius: 24px;
}
.products__item:nth-child(1){
   background-color: #AEC6A5;
}

.products__item::before{
        content: '';
        position: absolute;
        max-width: 200px;
        width: 100%;
        aspect-ratio: 1 / 1;
        border-radius: 50%;
        top: 0px;
        left: 50%;
        transform: translate(-50%, -55.3%);
}
.products__item::after{
    content: '';
    position: absolute;
    max-width: 200px;
    width: 100%;
    aspect-ratio: 1 / 1;
    border-radius: 50%;
    top: 0px;
    left: 50%;
    z-index: 2;
    transform: translate(-50%, -61%);
}

.products__item:nth-child(1)::before{
    background: linear-gradient(0deg, rgba(156, 190, 143, 0.7), rgba(156, 190, 143, 0.7)), #d9d9d9;
    box-shadow: 0px 8px 30px rgb(10 136 23 / 25%);
}

.products__item:nth-child(1)::after{
    background-image: url("../images/mobile/products-mobile-png/products01.png");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
}

.products__btn{
    background-image: url("../images/svgs/arrow-default.svg");
    background-repeat: no-repeat;
    background-position: 50% 50%;
    width: 41px;
    height: 16px;
    transition: background-image $transition ;

}
.products__btn:hover{
    background-image: url("../images/svgs/arrow-hover.svg");
    width: 51px;
    height: 16px;
}
.products__btn:focus{
    background-image: url("../images/svgs/arrow-focus.svg");
    width: 52px;
    height: 22px;
}

.products__item:nth-child(2) {
    background-color: #EFD478;
}
.products__item:nth-child(2)::before {
        background: linear-gradient(0deg, rgba(248, 210, 78, 0.7), rgba(248, 210, 78, 0.7)), #d9d9d9;
        box-shadow: 0px 8px 30px rgb(153 114 23 / 25%);
}
.products__item:nth-child(2)::after {
    background-image: url("../images/mobile/products-mobile-png/products02.png");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
}

.products__item:nth-child(3) {
    background-color: #D6936D;
}
.products__item:last-child::before {
    margin-right: 0px;
    background-color: #d6936d;
    background: linear-gradient(0deg, rgba(213, 117, 63, 0.7), rgba(213, 117, 63, 0.7)), #d9d9d9;
    box-shadow: 0px 8px 30px rgb(0 0 0 / 25%);
}
.products__item:last-child::after {
    background-image: url("../images/mobile/products-mobile-png/products01.png");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
}

.products__title {
    margin-bottom: 40px;
    @include font(700, 20px, 24px, 0.04em);
    text-align: center;
    text-transform: uppercase;
    
    color: $text-dark-color;
}

.products__wrapper{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    min-height: 234px;
    height: 100%;
}
.products__desc {
    padding-left: 40px;
    padding-right: 40px;
    @include font(400, 14px, 25px, 0.04em);
    text-align: center;
    color: $text-dark-color;
    
}
.products__btn{
    margin: 0 auto;
    border: none;
    
    background-color: transparent;
    color: inherit;
    
    cursor: pointer;
    display: inline-block;
}

